import React from "react";
import ChatbotComponent from "./components/Chatbot.js";
import ListComponenet from "./components/List.js";
import { Route, HashRouter, Switch } from "react-router-dom";
function App() {
  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={ListComponenet} />
        <Route exact path="/chatbot" component={ChatbotComponent} />
      </Switch>
    </HashRouter>
  );
}
export default App;
